import React, {Fragment, useEffect} from 'react';
import Sticky from 'react-stickynode';
import {HashRouter, Route, Switch, Link} from 'react-router-dom';

import Home from './pages/home/home';
import About from './pages/about/about';
import Why from './pages/why/why';
import Reviews from './pages/reviews/reviews';
import Services from './pages/services/services';
import Pricing from './pages/pricing/pricing';
import s from './pages/home/home.module.css';

function scrollIntoView(event, element) {
	event.preventDefault();
	document.getElementById(element).scrollIntoView();
	window.location.hash = `#/?section=${element}`;
}

let section;
try {
	section = window.location.hash.split('?section=')[1];
} catch (e) {
	section = null;
}

function App() {
	useEffect(function () {
		if (section) {
			const element = document.getElementById(section);
			if (element) {
				element.scrollIntoView();
			}
		}
	});
	return <Fragment>
		<HashRouter basename="/">
			<Switch>
				<Route exact path="/">
					<Home/>
					<Sticky enabled={true} top={0}>
						<nav id="main-nav" className={s.nav}>
							<a href="" className={s.navItem}>Home</a>
							<a href="#/?section=about"
								onClick={(e) => {
									scrollIntoView(e, 'about');
								}}
								className={s.navItem}
								to="/about">About</a>
							<a href="#/?section=why"
								onClick={(e) => {
									scrollIntoView(e, 'why');
								}}
								className={s.navItem}
								to="/why">Why</a>
							<a href="#/?section=reviews"
								onClick={(e) => {
									scrollIntoView(e, 'reviews');
								}}
								className={s.navItem}>Reviews</a>
							<a href="#/?section=services"
								onClick={(e) => {
									scrollIntoView(e, 'services');
								}}
								className={s.navItem}>Services</a>
							<Link to="/pricing"
								className={s.navItem}>Pricing</Link>
						</nav>
					</Sticky>
					<About/>
					<Why/>
					<Reviews/>
					<Services/>
				</Route>
				<Route exact path="/pricing">
					<Sticky enabled={true} top={0}>
						<nav className={s.pricingNav}>
							<a href="/" className={s.navItem}>Soulgineering</a>
						</nav>
					</Sticky>
					<Pricing/>
				</Route>
			</Switch>
		</HashRouter>
	</Fragment>;
}

export default App;
