import React, {Fragment, useRef, useEffect, useState} from 'react';

import reviews from './google-reviews.json';

import s from './google-reviews.module.css';
import sc from '../../common.module.css';
import star from '../../../graphics/star_rate-24px.svg';
import expandLess from '../../../graphics/expand_less-24px.svg';
import expandMore from '../../../graphics/expand_more-24px.svg';

// import photos
import photo1 from './photos/photo1.png';
import photo2 from './photos/photo2.png';

const photos = {photo1, photo2};

export default () => {

	const reviewsRef = useRef();

	function handleShowMore({
		paragraphWrapperIndex, 
		showMoreButtonIconIndex, 
		bottomButtonIndex, 
		scroll, 
		articleIndex
	}) {
		const wrapper = document.getElementById(paragraphWrapperIndex);
		const showMoreButtonIcon = document.getElementById(showMoreButtonIconIndex);
		const bottomButton = document.getElementById(bottomButtonIndex);
		if (showMoreButtonIcon.alt === 'Expand') {
			showMoreButtonIcon.setAttribute('src',expandLess);
			showMoreButtonIcon.setAttribute('alt','Collapse');
			wrapper.style.height = 'auto';
			bottomButton.style.display = 'block';
		} else {
			showMoreButtonIcon.setAttribute('src',expandMore);
			showMoreButtonIcon.setAttribute('alt','Expand');
			wrapper.style.height = '22vh'; // This must be the same value as in the cSS
			bottomButton.style.display = 'none';
		}
		
		if (scroll) {
			document.getElementById(articleIndex).scrollIntoView();
			window.scrollBy(0, document.getElementById('main-nav').offsetHeight * -1);
		}
	}

	return (
		<section className={s.googleReviewSection} ref={reviewsRef}>
			{
				reviews.map((review, rIndex) => {
					const articleIndex = `article-google-reviews-${rIndex}`;
					const paragraphWrapperIndex = `${articleIndex}-paragraph-wrapper`;
					const showMoreButtonIconIndex = `${articleIndex}-show-more-button-icon`;
					const bottomButtonIndex = `${articleIndex}-bottom-button`;
				    return (
						<article id={articleIndex} className={s.article} key={rIndex}>
							<a className={s.header}
								key={rIndex}
								href={review.share} target="_blank">
								<img className={s.photo} src={photos[review.photo]}/>
								<b>{review.name}</b>
							</a>
							<div className={s.fiveStarsWrapper}>
								<div className={s.fiveStars}>
									<img src={star} alt="star" />
									<img src={star} alt="star" />
									<img src={star} alt="star" />
									<img src={star} alt="star" />
									<img src={star} alt="star" />
								</div>
								<button className={`${sc.iconButton} .topButton`} onClick={() => {handleShowMore({paragraphWrapperIndex, showMoreButtonIconIndex, bottomButtonIndex});}}><img id={showMoreButtonIconIndex} src={expandMore} alt="Expand" /></button>
							</div>
							<div id={paragraphWrapperIndex} className={`paragraphWrapper ${s.paragraphWrapper}`}>
								<div>{review.paragraphs.map((paragraph, pIndex) => (<p key={pIndex}>{paragraph}</p>))}</div>
							</div>
							<button id={bottomButtonIndex} className={`${sc.iconButton} ${s.bottomButton}`} onClick={() => {handleShowMore({paragraphWrapperIndex, showMoreButtonIconIndex, bottomButtonIndex, scroll: true, articleIndex});}}><img id={showMoreButtonIconIndex} src={expandLess} alt="Collapse" /></button>
						</article>
					);
				})
			}
		</section>
	);
};