import React, {Fragment, useEffect, useRef} from 'react';

import KnowThyself from '../../graphics/KnowThyself.png';

import s from './about.module.css';
import sc from '../common.module.css';

export default () => {

	const sectionRef = useRef();

	/* Show the about section when the user scrolls. This keeps it
	 * from displaying on the home screen for all screen resolutions.
	 */
	useEffect(function () {
		window.addEventListener('scroll', function(e) {
			if (sectionRef.current) {
				sectionRef.current.style.visibility = 'visible';
				if (window.scrollY==0) {
					sectionRef.current.style.visibility = 'hidden';
				}
			}
		});

	});

	return 	(
		<Fragment>
			<div id="about" className={sc.topSectionSpacer} />
			<section className={s.aboutSection} ref={sectionRef}>
				<h1 className={s.aboutHeader}>About</h1>
				<div className={sc.backgroundMask}>
					<p>We are on a mission to grow empathy on planet Earth by healing trauma.</p>
					<p>With our new-age personal development program, we use astrology and coaching to help you</p>
					<ul>
						<li>Improve Self Worth</li>
						<li>Heal Trauma</li>
						<li>Mend Broken Relationships</li>
						<li>Begin New Relationships</li>
						<li>Find Purpose and Inner Peace</li>
					</ul>
				</div>
			</section>
			<img className={s.aboutImage} src={KnowThyself} alt="Know Thyself"/>
		</Fragment>
	);
};
